@charset "UTF-8";
@tailwind base;
@tailwind components;
@tailwind utilities;
@layer components {
  @media only screen and (max-width: 1920px) {
    .container {
      max-width: 100%;
    }
  }
}
/* // _general.scss */
/*********************************/
/*             General            */
/*===============================*/
section {
  overflow: hidden;
}

p {
  @apply leading-relaxed;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  @apply leading-normal;
}

::selection {
  @apply bg-indigo-600/90 text-white;
}

.section-subtitle {
  @apply text-primaryOrange 2xl:text-[1.32rem] font-medium uppercase mb-3;
}

.section-title {
  @apply 2xl:text-5xl text-3xl md:text-3xl lg:text-4xl  font-bold 2xl:leading-[4.25rem] text-lightText max-w-[920px] mx-auto mb-10 xl:mb-[4.6rem];
}

.section-title-lg {
  @apply 2xl:text-[3.375rem] text-3xl md:text-3xl lg:text-4xl  font-bold 2xl:leading-[4.25rem] text-lightText max-w-[920px] mx-auto mb-4 xl:mb-11;
}

.btn {
  @apply px-6 py-2.5 rounded-md flex items-center space-x-2 bg-primaryOrange text-lightText border-none font-medium !important;
}

/*********************************/
/*         Menu                  */
/*===============================*/
#topnav {
  @apply fixed right-0 left-0 top-0 z-999 bg-transparent border-0 transition-all duration-500 ease-in-out;
}
#topnav .logo-dark {
  display: none;
}
#topnav .navbar-toggle {
  @apply border-0 relative p-0 m-0 cursor-pointer;
}
#topnav .navbar-toggle .lines {
  @apply w-[25px] block relative h-[18px]  mr-0  ml-[10px];
}
#topnav .navbar-toggle span {
  transition: transform 0.5s ease;
  @apply h-[2px] w-full bg-primaryBlue dark:bg-white block mb-[5px];
}
#topnav .navbar-toggle span:last-child {
  @apply mb-0;
}
#topnav .navbar-toggle.open span {
  @apply absolute;
}
#topnav .navbar-toggle.open span:first-child {
  @apply top-[6px] rotate-[45deg];
}
#topnav .navbar-toggle.open span:nth-child(2) {
  @apply invisible;
}
#topnav .navbar-toggle.open span:last-child {
  @apply w-full top-[6px] -rotate-[45deg];
}
#topnav .navbar-toggle.open span:hover {
  @apply bg-indigo-600;
}
#topnav .navbar-toggle:hover, #topnav .navbar-toggle:focus,
#topnav .navbar-toggle .navigation-menu > li > a:hover {
  @apply bg-transparent;
}
#topnav .nav-right {
  @apply leading-[74px] text-white;
}
#topnav .nav-right > li {
  @apply leading-[initial];
}
#topnav .nav-right .login-btn-primary,
#topnav .nav-right .btn-icon-dark {
  @apply hidden;
}
#topnav .nav-right .login-btn-light,
#topnav .nav-right .btn-icon-light {
  @apply inline-block;
}
#topnav .nav-right .search-bar .menu-search form {
  @apply relative;
}
#topnav .nav-right .search-bar .searchform {
  @apply after:absolute after:right-[14px] after:top-[14px] after:text-xl after:leading-5 after:pointer-events-none;
}
#topnav .nav-right .search-bar .searchform input[type=text] {
  @apply shadow-none py-[10px] pr-[42px] pl-3 h-11 text-sm block outline-none  !important;
}
#topnav .nav-right .search-bar .searchform input[type=submit] {
  @apply hidden;
}
#topnav .nav-right .search-bar .searchform:after {
  content: "ó°‰";
  font-family: "Material Design Icons";
}
#topnav .navigation-menu {
  @apply list-none m-0 p-0;
}
#topnav .navigation-menu > li {
  @apply float-left block relative my-0 mx-[10px];
}
#topnav .navigation-menu > li > a {
  @apply block text-[#FFF7E9] dark:text-white text-base bg-transparent font-normal tracking-[1px] leading-6  px-2 py-6 !important;
}
#topnav .navigation-menu > li > a.active {
  @apply text-primaryBlue !important;
}
#topnav .navigation-menu > li:hover > a {
  @apply text-primaryOrange !important;
}
#topnav .navigation-menu > li .submenu.megamenu li .megamenu-head {
  @apply py-[10px] px-5 whitespace-nowrap text-xs uppercase tracking-[0.04em] font-bold text-slate-400 !important;
}
#topnav .menu-extras {
  @apply hidden;
}
#topnav.scroll {
  @apply bg-white dark:bg-slate-900 border-none shadow;
}
#topnav.scroll .navigation-menu > li > a {
  @apply text-black dark:text-white;
}
#topnav.scroll .navigation-menu > li > .menu-arrow {
  @apply border-black dark:border-white;
}
#topnav.scroll .navigation-menu > li:hover > a, #topnav.scroll .navigation-menu > li.active > a {
  @apply text-black !important;
}
#topnav.scroll .navigation-menu > li:hover > .menu-arrow, #topnav.scroll .navigation-menu > li.active > .menu-arrow {
  @apply border-indigo-600 dark:border-indigo-600;
}
#topnav.defaultscroll.dark-menubar .logo {
  @apply leading-[70px];
}
#topnav.defaultscroll.scroll .logo {
  @apply leading-[62px];
}
#topnav.nav-sticky {
  @apply bg-white dark:bg-slate-900 shadow dark:shadow-gray-800;
}
#topnav.nav-sticky .nav-right {
  @apply text-black;
}
#topnav.nav-sticky .navigation-menu > li > a {
  @apply text-black !important;
}
#topnav.nav-sticky .navigation-menu > li > a.active {
  @apply text-primaryBlue !important;
}
#topnav.nav-sticky .navigation-menu.nav-light > li > a {
  @apply text-black dark:text-white !important;
}
#topnav.nav-sticky .navigation-menu.nav-light > li.active > a {
  @apply text-indigo-600 dark:text-indigo-600 !important;
}
#topnav.nav-sticky .navigation-menu.nav-light > li:hover > .menu-arrow, #topnav.nav-sticky .navigation-menu.nav-light > li.active > .menu-arrow {
  @apply border-indigo-600 !important;
}
#topnav.nav-sticky .navigation-menu.nav-light > li:hover > a, #topnav.nav-sticky .navigation-menu.nav-light > li.active > a {
  @apply text-indigo-600 dark:text-indigo-600 !important;
}
#topnav.nav-sticky.tagline-height {
  @apply top-0 !important;
}
#topnav.nav-sticky .logo .l-dark {
  @apply inline-block;
}
#topnav.nav-sticky .logo .l-light {
  @apply hidden;
}

.logo {
  @apply font-bold text-[24px] mr-[15px] pt-0 px-[6px] pb-0 tracking-[1px];
}

@media (min-width: 1171px) {
  #topnav .nav-container {
    @apply pt-8;
  }
  #topnav.nav-sticky .nav-container {
    @apply pt-0;
  }
  #topnav .navigation-menu {
    @apply flex flex-wrap justify-center;
  }
  #topnav .navigation-menu > li > a.active {
    @apply text-primaryOrange !important;
  }
  #topnav .navigation-menu > li:hover > .menu-arrow {
    @apply text-white;
  }
  #topnav .navigation-menu > li:hover > a, #topnav .navigation-menu > li.active > a {
    @apply text-primaryOrange  !important;
  }
  #topnav .navigation-menu > li.last-elements .submenu {
    @apply left-auto right-0 before:left-auto before:right-[10px];
  }
  #topnav .navigation-menu.nav-light > li > a {
    @apply text-white/50 !important;
  }
  #topnav .navigation-menu.nav-light > li.active > a {
    @apply text-white !important;
  }
  #topnav .navigation-menu.nav-light > li:hover > .menu-arrow {
    @apply border-white !important;
  }
  #topnav .navigation-menu.nav-light > li:hover > a {
    @apply text-white !important;
  }
  #topnav .navbar-toggle {
    @apply hidden;
  }
  #topnav #navigation {
    @apply block !important;
  }
  #topnav.scroll {
    @apply top-0;
  }
  #topnav.scroll .navigation-menu > li > a {
    @apply py-5;
  }
  #topnav.scroll-active .navigation-menu > li > a {
    @apply py-[25px];
  }
  #topnav.nav-sticky .logo {
    display: none;
  }
  #topnav.nav-sticky .logo-dark {
    display: block;
  }
  #topnav.nav-sticky .navigation-menu {
    @apply flex flex-wrap justify-center;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu {
    transition: all 0.2s ease;
    @apply absolute top-full left-0 z-[1000] py-[15px] px-0 list-none min-w-[180px] invisible opacity-0 mt-[10px] rounded-md bg-white dark:bg-slate-900 shadow dark:shadow-gray-800;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu li {
    @apply relative;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu li a {
    transition: all 0.3s;
    @apply block py-[10px] px-5 clear-both whitespace-nowrap text-[11px] uppercase tracking-[0.04em] font-bold text-black dark:text-white leading-[17px];
  }
  #topnav.nav-sticky .navigation-menu > li .submenu li a:hover {
    @apply text-indigo-600 dark:text-indigo-600 !important;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu li ul {
    @apply list-none pl-0 m-0;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu.megamenu {
    @apply whitespace-nowrap left-1/2 -translate-x-1/2 fixed top-auto flex;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu.megamenu > li {
    @apply overflow-hidden align-top w-1/5;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu.megamenu > li .submenu {
    @apply left-full top-0 ml-[10px] -mt-px;
  }
  #topnav.nav-sticky .navigation-menu > li .submenu > li .submenu {
    @apply left-[101%] top-0 ml-[10px] -mt-px;
  }
  #topnav.nav-sticky .navigation-menu > li:hover > .menu-arrow {
    @apply text-white;
  }
  #topnav.nav-sticky .navigation-menu > li:hover > a, #topnav.nav-sticky .navigation-menu > li.active > a {
    @apply bg-transparent text-primaryBlue  !important;
  }
  #topnav.nav-sticky .navigation-menu > li.last-elements .submenu {
    @apply left-auto right-0 before:left-auto before:right-[10px];
  }
  #topnav.nav-sticky .navigation-menu.nav-light > li > a {
    @apply text-white/50 !important;
  }
  #topnav.nav-sticky .navigation-menu.nav-light > li.active > a {
    @apply text-primaryBlue !important;
  }
  #topnav.nav-sticky .navigation-menu.nav-light > li:hover > .menu-arrow {
    @apply border-primaryBlue !important;
  }
  #topnav.nav-sticky .navigation-menu.nav-light > li:hover > a {
    @apply text-primaryBlue !important;
  }
}
@media (max-width: 1170px) {
