  }
  #topnav .logo {
    @apply hidden;
  }
  #topnav .logo-dark {
    @apply block;
  }
  #topnav .nav-right {
    @apply text-black;
  }
  #topnav .container {
    @apply w-auto;
  }
  #topnav #navigation {
