  #topnav .navigation-menu > li .submenu .submenu.open {
    @apply block;
  }
  #topnav .navigation-menu > li > a {
    @apply text-black dark:text-white py-[10px] px-[20px] after:absolute after:right-[15px];
  }
  #topnav .menu-extras {
    @apply block !important;
  }
  #topnav .menu-extras .menu-item {
    @apply block border-gray-200 dark:border-gray-700;
  }
  #topnav .navbar-header {
    @apply float-left;
  }

  #navigation {
    @apply absolute top-[65px] left-0 w-full h-0 opacity-0 pb-4 bg-white dark:bg-slate-900 overflow-auto transition-all duration-300;
  }
  #navigation.open {
    @apply h-[230px] opacity-100;
  }
}
@media (max-width: 768px) {
  #topnav {
    @apply py-0;
  }
  #topnav .nav-container {
    @apply py-4;
  }
  #topnav .logo,
#topnav .logo-dark {
    @apply max-w-[100px];
  }

  .nav-right {
    @apply hidden !important;
  }

  #navigation {
    @apply top-[50px];
  }
}
@media (min-width: 768px) {
  .navbar-toggle {
    @apply block;
  }
}
@media (max-width: 425px) {
  #topnav .buy-menu-btn {
    @apply block my-0 mx-[10px] py-[10px] px-5 !important;
  }
  #topnav .buy-menu-btn .dropdown .dropdown-menu.show {
    transform: translate3d(0px, -54px, 0px) !important;
  }
}
.tagline {
  @apply absolute w-full z-[99] text-sm py-[13px] px-0;
}

@media screen and (max-width: 575px) {
  .tagline {
    @apply hidden;
  }

  .tagline-height {
    @apply top-0 !important;
  }
}
@media (min-width: 576px) {
  .tagline-height {
    @apply top-[47px] !important;
  }
}
.sidebar-nav > .navbar-item:hover .navbar-link, .sidebar-nav > .navbar-item.active .navbar-link {
  @apply text-indigo-600;
}
.sidebar-nav li.active a {
  @apply text-indigo-600;
}
.sidebar-nav li.account-menu.active .navbar-link, .sidebar-nav li.account-menu:hover .navbar-link {
  @apply text-indigo-600 dark:text-white !important;
}

